<template>
  <v-form ref="form" @submit.prevent>
    <v-container>
      <v-row align="center" justify="center">
        <v-subheader class="text-center">Receber Cesta Básica</v-subheader>
      </v-row>
      <v-row align-center hidden>
        <v-col class="d-flex" cols="6">
          <v-text-field v-model="this.recebimento.cdRecebimento" label="Id" outlined disabled></v-text-field>
        </v-col>
      </v-row>
      <v-row align="center" justify="center">
        <v-col cols="12" sm="12" md="8" lg="6" xl="6">
          <v-combobox
          :items="intencoes"
          :hint="`${formatedHint(this.recebimento.intencao.dtIntencao, 'Data da Solicitação: ' + this.recebimento.intencao.dtIntencao)}`"
          persistent-hint
          v-model="recebimento.intencao"
          item-text="codigo"
          item-value="cdIntencao"
          label="Intenção - Código"
          placeholder="Escolha a Intenção de Doar"
          return-object
          dense
          outlined
          required></v-combobox>
        </v-col>
      </v-row>
      <v-row align="center" justify="center">
        <v-col class="d-flex" cols="6">
          <v-combobox
            :items="this.doadores"
            :hint="`${'CPF:' + this.recebimento.intencao.doador.cpf}`"
            persistent-hint
            v-model="this.recebimento.intencao.doador"
            item-text="nome"
            item-value="cdUsuario"
            label="Doador"
            placeholder="Escolha o Doador"
            return-object
            dense
            disabled
            outlined
            required></v-combobox>
        </v-col>
      </v-row>
      <v-row align="center" justify="center">
        <v-col class="d-flex" cols="6">
          <v-combobox
          :items="this.instituicoes"
          :hint="`${formatedHint2}`"
          persistent-hint
          v-model="recebimento.intencao.instituicao"
          item-text="nome"
          item-value="cdUsuario"
          label="Instituição"
          placeholder="Escolha a Instituição"
          return-object
          dense
          disabled
          outlined
          required></v-combobox>
        </v-col>
      </v-row>
      <v-row align="center" justify="center">
        <v-col class="d-flex" cols="6">
          <v-combobox
            :items="tpCestas"
            :hint="`${recebimento.intencao.tpCesta.descricao}`"
            persistent-hint
            v-model="recebimento.intencao.tpCesta"
            item-text="nome"
            item-value="id"
            label="Tipo de Cesta"
            placeholder="Escolha o Tipo de Cesta"
            return-object
            dense
            disabled
            outlined
            required></v-combobox>
        </v-col>
      </v-row>
      <v-row align="center" justify="center">
        <v-col class="d-flex" cols="6">
          <v-text-field
            v-model="recebimento.nfCesta"
            :counter="150"
            label="Nota Fiscal"
            placeholder="Número da Nota Fiscal"
            dense
            outlined
            required
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row align="center" justify="center">
        <v-col class="d-flex" cols="6" >
            <v-slider
              v-model="recebimento.qtdCestas"
              type="number"
              min=1
              max=50
              :value="`${recebimento.qtdCestas}`"
              thumb-label="always"
              label="Qtd. Cestas"
              dense
              outlined
              required></v-slider>
          </v-col>
        </v-row>
        <v-row align="center" justify="end">
          <v-col class="d-flex" cols="6" >
            <v-btn color="success" class="" @click="save">Salvar</v-btn>
          </v-col>
        </v-row>
    </v-container>
  </v-form>
</template>

<script>
import DataService from "../service/RecebimentoDataService";
import DoadorDataService from "../service/DoadorDataService";
import InstituicaoDataService from "../service/InstituicaoDataService";
import IntencaoDataService from "../service/IntencaoDataService";
import TipoCestaDataService from "../service/TipoCestaDataService";

export default {
  data: () => ({
    doadores: [],
    instituicoes: [],
    intencoes: [],
    tpCestas: [],
    recebimento: {
      cdRecebimento:0,
      codigo: '',
      dtRecebimento:'',
      qtdCestas:0,
      nfCesta: '',
      intencao: {
        cdIntencao:0,
        qtdCestas: 0,
        status:0,
        tpCesta:{id:0,nome:'', descricao:''}, 
        doador: {
          cdUsuario:0,
          nome:'',
          login:'',
          senha:'',
          cpf:'',
          rua:'',
          numCasa:'',
          bairro:{
            id:0,nome:'',
            cidade: {
              id:0,
              nome:'',
              uf:{
                id:0,
                sigla:'',
                nome:''
              }
            }
          }
        },
        instituicao:{
          cdUsuario:0,
          nome:'',
          senha:'',
          cnpj:'',
          rua:'',
          numImovel:'',
          bairro:{
            id:0,
            nome:'',
            cidade: {
              id:0,
              nome:'',
              uf:{
                id:0,
                sigla:'',
                nome:''
              }
            }
          }
        }
      }
    }
  }),
  computed: {
    id() {
      return this.$route.params.id;
    },
    now() {
      return new Date().toISOString()
    },
    formatedHint2() {
      return this.recebimento.intencao.instituicao.bairro.nome + ' - ' + this.recebimento.intencao.instituicao.bairro.cidade.nome + ' - ' + this.recebimento.intencao.instituicao.bairro.cidade.uf.nome
    },
    //...mapState({})
  },
  watch: {

  },
  methods: {
    formatedHint(cond, desc) {
      return (cond) ? desc : ''
    },
    initialize() {
      if(this.id > 0){
        DataService.retrieve(this.id).then(response => {
          this.recebimento = response.data
        });
      }
      DoadorDataService.retrieveAll().then(response => {
        this.doadores = response.data
      });
      InstituicaoDataService.retrieveAll().then(response => {
        this.instituicoes = response.data
      });
      IntencaoDataService.retrieveAll().then(response => {
        this.intencoes = response.data
      });
      TipoCestaDataService.retrieveAll().then(response => {
        this.tpCestas = response.data
      });
    },
    save(e) {
      e.preventDefault();
      if(this.id > 0){
        DataService.update(this.id, this.recebimento, {
          id: this.id,
          recebimento: this.recebimento
        }).then(() => {
          this.$router.push('/recebimentos')
        }).catch(e => {
          console.log(e)
          if(e.response){
            this.$store.dispatch('setSnackbar', {
              showing: true, text: e.response.data.message, timeout: 8000, color: 'error'
            });
          }
        });
      } else {
        this.recebimento.dtRecebimento = this.now
        this.recebimento.intencao.status = 1
        DataService.create(this.recebimento, {
          recebimento: this.recebimento
        }).then(response => {
          console.log(response)
          this.$router.push('/recebimentos')
        }).catch(e => {
          console.log(e)
          if(e.response){
            this.$store.dispatch('setSnackbar', {
              showing: true, text: e.response.data.message, timeout: 8000, color: 'error'
            });
          }
        });
      }
    }
  },
  created() {
    this.initialize()
  }
};
</script>

